.item {
  display: flex;
  padding: 10px;
  background-color: #ffffff;
  border-bottom: 1px solid #e5e5e5; }
  .item p {
    margin: 0;
    padding: 0; }
  .item-type {
    background-color: #80bd01;
    color: #fff;
    width: 34px;
    height: 18px;
    display: flex;
    align-items: center;
    font-size: 12px;
    justify-content: center;
    padding: 2px 4px;
    min-width: 34px;
    box-sizing: border-box;
    border-radius: 4px;
    margin-right: 4px;
    margin-top: 1%; }
    .item-type.ask, .item-type.share {
      background-color: #e5e5e5;
      color: #9c9c9c; }
  .item-title-wrap {
    flex: 1;
    display: flex;
    flex-flow: column;
    width: 70%; }
  .item-title {
    flex: 1;
    color: #3f51b5;
    font-size: 13px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: 22px;
    vertical-align: middle; }
  .item-count {
    flex: 1;
    display: flex;
    align-items: center;
    color: gray;
    font-size: 13px;
    justify-content: space-between; }

.author-img {
  width: 45px;
  height: 45px;
  border-radius: 5px;
  margin-right: 6px; }

.topics-tabs {
  height: 46px;
  background-color: #2196f3;
  color: #fff;
  font-size: 18px; }
  .topics-tabs ul {
    display: flex;
    align-items: center;
    height: 100%; }
    .topics-tabs ul li {
      flex: 1;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      border-bottom: 2px solid #2196f3; }
      .topics-tabs ul li.active {
        border-color: red;
        font-size: 120%;
        font-weight: 700; }

.app .am-list-header {
  padding: 0; }

.am-list-view-scrollview-content.am-list {
  max-width: 100%; }
